z-index: 10;\n      }\n    }\n\n    &:hover {\n      & span {\n        &.tooltip {\n          display: block;\n        }\n      }\n    }\n\n    &.active-arrow {\n      position: relative;\n      &::after {\n        content: \"\";\n        display: inline-block;\n        width: 18px;\n        height: 18px;\n        background-color: white;\n        position: absolute;\n        left: 50%;\n        bottom: -23px;\n        transform: translateX(-50%) rotate(45deg);\n        box-shadow: 4px 6px 13px 0 rgba(0, 0, 0, 0.5);\n      }\n    }\n  }\n\n  & > div {\n    display: flex;\n    flex-direction: row;\n    margin: 0;\n\n    & > div {\n      &:first-child {\n      }\n      &:last-of-type {\n        button {\n          margin-right: 0;\n        }\n      }\n      position: relative;\n    }\n  }\n\n  input[type=\"number\"] {\n    border: none;\n  }\n}\n"],"sourceRoot":""}]);
