n      }\n\n      &-name {\n        font-size: 1.2rem;\n        font-weight: 800;\n        line-height: 1.2;\n        color: var(--main-black);\n        margin-bottom: 0;\n\n        @media (min-width: 768px) {\n          font-size: 1.4rem;\n        }\n      }\n\n      &-variant {\n        opacity: 0.8;\n        text-transform: lowercase;\n      }\n\n      &-material {\n        font-size: 0.8rem;\n      }\n\n      @media (min-width: 768px) {\n        &-material {\n          font-size: 1rem;\n        }\n\n        &-size {\n          font-size: 1rem;\n        }\n      }\n    }\n\n    @media (max-width: 768px) {\n      padding: 0;\n    }\n  }\n\n  @media (max-width: 768px) {\n    padding: 0;\n  }\n}\n\n.product-layout-options {\n  padding: 0;\n\n  @media (min-width: 768px) {\n    padding: 5px 15px 15px;\n  }\n\n  &__wrapper {\n    border-radius: 4px;\n    .control-group-title {\n      color: var(--main-grey-dark);\n      display: inline-block;\n      width: 100%;\n      text-align: center;\n    }\n  }\n\n  &__wrapper {\n    border-radius: 4px;\n    .control-group-title {\n      color: var(--main-grey-dark);\n      display: inline-block;\n      width: 100%;\n      text-align: center;\n    }\n  }\n\n  .add-row-button {\n    display: inline-block;\n    border: 1px solid var(--main-border);\n    border-radius: 15px;\n    background: white;\n    box-shadow: 1px 1px 5px -3px rgba(50, 50, 50, 0.35);\n\n    cursor: pointer;\n\n    &:hover {\n      color: var(--main-black);\n    }\n  }\n\n  &-visual {\n    max-width: 650px;\n    width: 100%;\n    padding: 24px;\n    border-radius: 4px;\n    background-color: white;\n    box-shadow: -2px 11px 10px 0 rgba(0, 0, 0, 0.5);\n    position: absolute;\n    top: 0;\n    left: 50%;\n    z-index: 12;\n    transform: translateX(-50%);\n\n    .font-item {\n      .button {\n        &:hover {\n          background-color: #f7f7f7;\n        }\n      }\n    }\n\n    @media (min-width: 768px) {\n      top: 70px;\n    }\n\n    .type-tester {\n      display: flex;\n      justify-content: center;\n      align-items: center;\n      flex-direction: column;\n      margin-bottom: 8px;\n      width: 100%;\n      height: 120px;\n      border-radius: 8px;\n      background-color: var(--icy-blue);\n      position: relative;\n\n      & > span {\n        padding: 16px;\n      }\n\n      &__font-name {\n        font-size: 1rem;\n        font-family: system-ui, -apple-system, BlinkMacSystemFont, \"Segoe UI\",\n          Roboto, Oxygen, Ubuntu, Cantarell, \"Open Sans\", \"Helvetica Neue\",\n          sans-serif;\n        border-bottom: 1px solid #808080;\n      }\n\n      &__font-test {\n        & > div {\n          font-size: 2.5rem;\n          overflow: hidden;\n          white-space: nowrap;\n          text-align: left;\n          text-overflow: ellipsis;\n          white-space: nowrap;\n\n          line-height: 1.2;\n          max-width: 300px;\n        }\n\n        &.text-uppercase {\n          text-transform: uppercase;\n        }\n      }\n\n      &__available-styles {\n        display: flex;\n        flex-direction: row;\n        gap: 4px;\n        position: absolute;\n        right: 4px;\n        bottom: 4px;\n\n        & > div {\n          opacity: 0.2;\n          font-size: 0.8rem;\n          line-height: 18px;\n          text-align: center;\n          display: inline-block;\n          width: 20px;\n          height: 20px;\n          min-width: 20px;\n          padding: 0;\n          border-radius: 4px;\n          border: 1px solid rgba(0, 0, 0, 0.3);\n          background-color: transparent;\n          transition: opacity 0.3s ease-in-out;\n          &.enabled {\n            opacity: 1;\n          }\n        }\n        @media (min-width: 992px) {\n          & > div {\n            font-size: 1rem;\n            line-height: 28px;\n            width: 30px;\n            height: 30px;\n            min-width: 30px;\n          }\n        }\n      }\n\n      @media (min-width: 768px) {\n        &__font-test {\n          font-size: 3rem;\n        }\n      }\n    }\n\n    .button {\n      &.button__close {\n        font-size: 0.9rem;\n        padding: 0 16px;\n        margin: 0;\n        border: 1px solid #ddd;\n        border-radius: 20px;\n        background: white;\n        cursor: pointer;\n        position: absolute;\n        top: 10px;\n        right: 10px;\n        z-index: 10;\n        &:hover {\n          background-color: #f7f7f7;\n        }\n      }\n    }\n\n    .product-layout-options {\n      display: flex;\n\n      & > aside {\n        flex: 1;\n\n        .button {\n          transition: background 0.3s ease-in-out;\n          &.active {\n            background-color: var(--icy-blue);\n          }\n        }\n\n        .sizes__list,\n        .font__list {\n          display: flex;\n          flex-wrap: wrap;\n          justify-content: center;\n          align-items: center;\n          gap: 8px;\n        }\n\n        @media (min-width: 768px) {\n          .sizes__list,\n          .font__list {\n            display: flex;\n            flex-wrap: wrap;\n            gap: 16px;\n          }\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
