../../images/icons/icon-delivery.svg);\n}\n\n.icon-information {\n  background-image: url(../../../images/icons/icon-information.svg);\n}\n\n.product-information {\n  display: flex;\n  flex-direction: column;\n  border-top: 1px solid #eee;\n  border-bottom: 1px solid #eee;\n  background-color: #fff;\n\n  &__mobile--wrapper {\n    .product-description {\n      position: relative;\n      .button-toggle-description {\n        position: absolute;\n        left: 50%;\n        bottom: 10px;\n        transform: translateX(-50%);\n      }\n      &-folded {\n        height: 116px;\n        overflow: hidden;\n        &::before {\n          content: \"\";\n          display: block;\n          width: 100%;\n          height: 100%;\n          background: rgb(247, 247, 247);\n          background: linear-gradient(\n            0deg,\n            rgba(247, 247, 247, 1) 0%,\n            rgba(247, 247, 247, 0) 100%\n          );\n          position: absolute;\n          top: 0;\n          left: 0;\n        }\n      }\n    }\n\n    @media (min-width: 768px) {\n      display: none;\n    }\n  }\n\n  & > div {\n    font-size: 0.85rem;\n    display: flex;\n    flex: 1;\n    padding: 8px 0;\n    border-bottom: 1px solid #eee;\n\n    &:last-of-type {\n      border: none;\n    }\n\n    a {\n      color: var(--black);\n      border-bottom: 1px solid var(--black);\n    }\n  }\n\n  .icon {\n    width: 50px;\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-position: center;\n  }\n\n  .service-logos {\n    display: flex;\n    gap: 4px;\n\n    .service-logo {\n      width: 50px;\n      height: 21px;\n      background-size: contain;\n    }\n  }\n}\n\n.accordion {\n  border-bottom: 1px solid #ddd;\n\n  &__panel {\n    font-size: 0.85rem;\n    padding: 8px;\n    border-bottom: 1px solid #eee;\n\n    & > div {\n      padding: 0 8px;\n\n      & > div {\n        padding: 8px 0;\n      }\n    }\n\n    ul {\n      list-style-type: none;\n      margin: 0;\n      padding: 0;\n    }\n  }\n\n  &__panel--white {\n    padding: 0 8px 8px;\n\n    & > div {\n      padding: 16px;\n      border-radius: 8px;\n      background-color: #fff;\n\n      & > div {\n        border-bottom: 1px solid #eee;\n\n        &:last-of-type {\n          border: none;\n        }\n      }\n    }\n  }\n\n  &__button {\n    font-size: 1.4rem;\n    text-align: left;\n    width: 100%;\n    padding: 8px 16px;\n    border: none;\n    background-color: transparent;\n    position: relative;\n\n    &::after {\n      content: \"\";\n      display: inline-block;\n      width: 10px;\n      height: 10px;\n      border-left: 2px solid #43454b;\n      border-bottom: 2px solid #43454b;\n      position: absolute;\n      top: calc(50% - 5px);\n      right: 22px;\n      transform-origin: center;\n      transform: rotate(-135deg) translateY(-50%);\n      transition: transform ease-in-out 0.3s;\n    }\n\n    &:hover {\n      color: black;\n    }\n  }\n\n  &__open {\n    .accordion__button {\n      &::after {\n        transform: rotate(-45deg) translateY(-50%);\n      }\n    }\n  }\n}\n\n.reserved-for-typos {\n  padding: 8px 16px;\n}\n\n.more-product-information {\n  display: flex;\n\n  @media (max-width: 768px) {\n    padding: 0;\n  }\n\n  ul {\n    list-style-type: none;\n    margin: 0;\n    padding: 0;\n  }\n\n  a {\n    color: var(--theme-link-color);\n    text-decoration: underline;\n  }\n\n  @media (min-width: 768px) {\n    &.show {\n      display: flex;\n    }\n  }\n\n  .in-stock::before {\n    content: \"\";\n    display: inline-block;\n    vertical-align: initial;\n    width: 11px;\n    height: 11px;\n    margin-left: 5px;\n    margin-right: 5px;\n    border-radius: 100%;\n    background-color: var(--main-green);\n  }\n\n  .terms {\n    margin-top: -12px;\n\n    & > div {\n      padding-top: 0.5rem;\n      margin-top: 0.5rem;\n      border-top: 1px solid #ddd;\n\n      &:first-of-type {\n        margin-top: 0;\n        padding-top: 0;\n      }\n\n      .price {\n        font-weight: 600;\n        color: var(--orange);\n\n        .price__original {\n          font-size: 1rem;\n          text-decoration: line-through;\n          opacity: 0.5;\n          margin-left: 6px;\n        }\n      }\n    }\n\n    @media (min-width: 992px) {\n      margin-top: -8px;\n\n      & > div {\n        &:first-of-type {\n          border-top: none;\n        }\n      }\n    }\n  }\n\n  .base-information {\n    margin-bottom: 1rem;\n\n    &__header {\n      padding-bottom: 0;\n\n      h2 {\n        margin-bottom: 0;\n      }\n    }\n\n    &__description {\n      @media (max-width: 768px) {\n        padding-top: 0;\n        background-color: #fff;\n      }\n    }\n\n    &__technical {\n      padding-top: 70px;\n\n      @media (max-width: 768px) {\n        padding-top: 0;\n        background-color: #fff;\n      }\n    }\n  }\n\n  &-trigger {\n    text-align: center;\n\n    button {\n      font-weight: 600;\n      color: var(--main-black);\n      border: none;\n      background-color: transparent;\n\n      &.active {\n        &::after {\n          transform: rotate(-180deg);\n        }\n      }\n\n      &::after {\n        content: \"\";\n        display: inline-block;\n        width: 15px;\n        height: 15px;\n        margin-left: 5px;\n        background-image: url(\"../../../images/svg/arrow-down.svg\");\n        background-size: contain;\n      }\n    }\n\n    @media (max-width: 768px) {\n      display: none;\n    }\n  }\n}\n"],"sourceRoot":""}]);
